export const TOKENS_CSS = ":root {\n  --dock-bg: #ffffff;\n  --dock-stroke: rgba(0,0,0,0.08);\n  --dock-fg: #111111;\n  --dock-fg-2: #5F6368;\n  --accent: #3B82F6;\n  --success: #10B981;\n  --error: #EF4444;\n  --on-accent: #ffffff;\n  --on-strong: #ffffff;\n  --glass-bg: color-mix(in srgb, var(--dock-bg) 85%, transparent);\n  --surface: color-mix(in srgb, var(--dock-fg) 5%, transparent);\n  --surface-hover: color-mix(in srgb, var(--dock-fg) 8%, transparent);\n  --shadow: 0 12px 48px -12px rgba(0,0,0,0.12);\n  --shadow-lg: 0 24px 64px -16px rgba(0,0,0,0.16);\n  --radius-panel: 18px;\n  --radius-chip: 8px;\n  --header-height: 32px;\n}\n:root.dark-dock, .dark-dock, .dark {\n  --dock-bg: #161618;\n  --dock-stroke: rgba(255,255,255,0.12);\n  --dock-fg: #F5F5F7;\n  --dock-fg-2: #B0B3B8;\n  --accent: #60A5FA;\n  --success: #34D399;\n  --error: #F87171;\n  --on-accent: #ffffff;\n  --on-strong: #ffffff;\n  --glass-bg: color-mix(in srgb, var(--dock-bg) 85%, transparent);\n  --surface: color-mix(in srgb, var(--dock-fg) 6%, transparent);\n  --surface-hover: color-mix(in srgb, var(--dock-fg) 10%, transparent);\n  --shadow: 0 16px 48px -12px rgba(0,0,0,0.4);\n  --shadow-lg: 0 24px 64px -16px rgba(0,0,0,0.6);\n  --radius-panel: 18px;\n  --radius-chip: 8px;\n  --header-height: 32px;\n}\n";
